.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid var(--primary);
  border-radius: 20px;
  background: var(--primary);
  color: var(--secondary);
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: var(--menu);
  border-color: var(--menu);
}

.button--inverse {
  background: transparent;
  color: var(--primary);
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #ff0055;
}

.button--danger {
  background: #830000;
  border-color: #830000;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}
